n-items: center;\r\n  left: 0;\r\n  top: 0;\r\n  width: 100%;\r\n  overflow-y: scroll;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.4);\r\n}\r\n\r\n/* Modal Content */\r\n.modal-block__content {\r\n  margin-top: 20rem;\r\n  margin-bottom: 2rem;\r\n  position: relative;\r\n  width: 506px;\r\n  height: auto;\r\n  background-color: white;\r\n  color: black;\r\n  text-align: center;\r\n  padding: 56px 90px;\r\n  border-radius: 4px;\r\n  animation-name: animatetop;\r\n  animation-duration: 0.4s;\r\n}\r\n\r\n/* getting close icon */\r\n.modal-block__content > .close-icon {\r\n  position: absolute;\r\n  top: 24px;\r\n  right: 26px;\r\n}\r\n\r\n/* getting title */\r\n.modal-block__content > h5 {\r\n  font-weight: 400;\r\n  font-size: 20px;\r\n  line-height: 30px;\r\n  margin-bottom: 25px;\r\n}\r\n/* getting little title */\r\n.modal-block__content > h6 {\r\n  font-weight: 400;\r\n  font-size: 16px;\r\n  line-height: 24px;\r\n  margin-top: 30px;\r\n}\r\n\r\n.modal-block__inputs {\r\n  width: 330px;\r\n  background-color: #f2f2f2;\r\n  height: 42px;\r\n  border-radius: 4px;\r\n  border: none;\r\n  outline: none;\r\n  padding: 9px 17px;\r\n  margin-top: 17px;\r\n\r\n  font: 400 16px 'Poppins';\r\n  line-height: 24px;\r\n}\r\n.modal-block__inputs:focus {\r\n  border: 2px solid #5ab0ff;\r\n}\r\n/* getting placeholder */\r\n.modal-block__inputs::placeholder {\r\n  font-size: 16px;\r\n  line-height: 24px;\r\n  color: #7d7d7d;\r\n}\r\n\r\n/* error text */\r\n.modal-block__inputs + .error,\r\n.modal-block__checkbox > .error {\r\n  font-weight: 400;\r\n  font-size: 12px;\r\n  line-height: 18px;\r\n  color: #ff5437;\r\n  float: left;\r\n  margin-left: 15px;\r\n}\r\n.modal-block__checkbox > .error {\r\n  margin-top: -20px;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n/* all plans div */\r\n.modal-block__plan-block {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 11px;\r\n}\r\n/* text 'your plan' */\r\n.modal-block__plan-text {\r\n  margin-top: 23px;\r\n}\r\n/* plans text (label) */\r\n.modal-block__plan {\r\n  position: relative;\r\n  cursor: pointer;\r\n  padding: 9px 0;\r\n  border-radius: 4px;\r\n  background-color: #fafafa;\r\n  border: 1px solid #0278ed;\r\n  color: #0278ed;\r\n\r\n  font-weight: 400;\r\n  font-size: 16px;\r\n  line-height: 24px;\r\n}\r\n\r\n/* start radio styles */\r\n.modal-block__plan-block > input[type='radio'] {\r\n  display: none;\r\n}\r\n/* start radio styles */\r\n\r\n/* for blue checked radio */\r\n.modal-block__plan-block\r\n  > input[type='radio']:checked\r\n  + label[class*='plan__blue'] {\r\n  background-color: #0278ed;\r\n  border: 1px solid #0278ed;\r\n  color: white;\r\n}\r\n/* for red checked radio */\r\n.modal-block__plan-block\r\n  > input[type='radio']:checked\r\n  + label[class*='plan__red'] {\r\n  background-color: #ff5437;\r\n  border: 1px solid #ff5437;\r\n  color: white;\r\n}\r\n/* for yellow checked radio */\r\n.modal-block__plan-block\r\n  > input[type='radio']:checked\r\n  + label[class*='plan__yellow'] {\r\n  background-color: #ff9209;\r\n  border: 1px solid #ff9209;\r\n  color: white;\r\n}\r\n/* for input which type is radio */\r\n.modal-block__plan-block > input[type='radio'] + label::before {\r\n  content: '';\r\n  position: absolute;\r\n  left: 13px;\r\n  display: inline-block;\r\n  width: 24px;\r\n  height: 24px;\r\n  background-repeat: no-repeat;\r\n  background-position: center center;\r\n  background-size: 80% 80%;\r\n  background-image: none;\r\n}\r\n\r\n/* for checked radio */\r\n.modal-block__plan-block > input[type='radio']:checked + label::before {\r\n  background-image: url('../../assets/svg/check-icon.svg');\r\n}\r\n\r\n/* end radio styles */\r\n\r\n.plan__blue {\r\n  border: 1px solid #0278ed;\r\n  color: #0278ed;\r\n}\r\n\r\n.plan__red {\r\n  border: 1px solid #ff5437;\r\n  color: #ff5437;\r\n}\r\n\r\n.plan__yellow {\r\n  border: 1px solid #ff9209;\r\n  color: #ff9209;\r\n}\r\n\r\n/* check icon */\r\n#current-plan {\r\n  position: absolute;\r\n  left: 15px;\r\n}\r\n\r\n/* checkbox block */\r\n.modal-block__checkbox {\r\n  margin-top: 23px;\r\n  align-items: center;\r\n  justify-content: center;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.checkbox {\r\n  cursor: pointer;\r\n  margin-bottom: 0.2em;\r\n  width: 80px;\r\n  margin-left: -0.2em;\r\n}\r\n\r\n/* all inputs */\r\n.custom-checkbox > input {\r\n  position: absolute;\r\n  z-index: -1;\r\n  opacity: 0;\r\n}\r\n\r\n/* for label label, in .custom-checkbox */\r\n.custom-checkbox > span {\r\n  cursor: pointer;\r\n  display: inline-flex;\r\n  align-items: center;\r\n  user-select: none;\r\n}\r\n\r\n/* creating empty box for checkbox */\r\n.custom-checkbox > input[type='checkbox'] + span::before {\r\n  content: '';\r\n  display: inline-block;\r\n  width: 25px;\r\n  height: 25px;\r\n  flex-shrink: 0;\r\n  flex-grow: 0;\r\n  border: 2px solid #0278ed;\r\n  border-radius: 4px;\r\n  margin-right: 0.5em;\r\n  background-repeat: no-repeat;\r\n  background-position: center center;\r\n  background-size: 80% 80%;\r\n}\r\n\r\n/* adding check-icon for checked  */\r\n.custom-checkbox > input[type='checkbox']:checked + span::before {\r\n  border: 2px solid #0b76ef;\r\n  background-color: white;\r\n  background-image: url('../../assets/svg/check-blue-icon.svg');\r\n}\r\n\r\n.modal-block__send-btn {\r\n  margin-left: 0;\r\n  margin-top: 42px;\r\n}\r\n\r\n.modal-block__content > .loading-block {\r\n  margin-top: 40px;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: center;\r\n  gap: 13px;\r\n}\r\n\r\n/* getting loading text */\r\n.modal-block__content > .loading-block > h6 {\r\n  padding-top: 2px;\r\n\r\n  font-weight: 400;\r\n  line-height: 18px;\r\n  letter-spacing: 2px;\r\n  text-transform: uppercase;\r\n  color: #7e7e7e;\r\n}\r\n\r\n#loading-icon {\r\n  -webkit-animation-name: rotation;\r\n  -webkit-animation-duration: 2s;\r\n  -webkit-animation-iteration-count: infinite;\r\n  -webkit-animation-timing-function: linear;\r\n  -moz-animation-name: rotation;\r\n  -moz-animation-duration: 2s;\r\n  -moz-animation-iteration-count: infinite;\r\n  -moz-animation-timing-function: linear;\r\n  -o-animation-name: rotation;\r\n  -o-animation-duration: 2s;\r\n  -o-animation-iteration-count: infinite;\r\n  -o-animation-timing-function: linear;\r\n  animation-name: rotation;\r\n  animation-duration: 2s;\r\n  animation-iteration-count: infinite;\r\n  animation-timing-function: linear;\r\n}\r\n\r\n/* Add Animation */\r\n@keyframes animatetop {\r\n  from {\r\n    top: 300px;\r\n    opacity: 0;\r\n  }\r\n  to {\r\n    top: 0;\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n@-webkit-keyframes rotation {\r\n  0% {\r\n    -webkit-transform: rotate(0deg);\r\n    -moz-transform: rotate(0deg);\r\n    -o-transform: rotate(0deg);\r\n    transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    -webkit-transform: rotate(360deg);\r\n    -moz-transform: rotate(360deg);\r\n    -o-transform: rotate(360deg);\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n@-moz-keyframes rotation {\r\n  0% {\r\n    -webkit-transform: rotate(0deg);\r\n    -moz-transform: rotate(0deg);\r\n    -o-transform: rotate(0deg);\r\n    transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    -webkit-transform: rotate(360deg);\r\n    -moz-transform: rotate(360deg);\r\n    -o-transform: rotate(360deg);\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n@-o-keyframes rotation {\r\n  0% {\r\n    -webkit-transform: rotate(0deg);\r\n    -moz-transform: rotate(0deg);\r\n    -o-transform: rotate(0deg);\r\n    transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    -webkit-transform: rotate(360deg);\r\n    -moz-transform: rotate(360deg);\r\n    -o-transform: rotate(360deg);\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n@keyframes rotation {\r\n  0% {\r\n    -webkit-transform: rotate(0deg);\r\n    -moz-transform: rotate(0deg);\r\n    -o-transform: rotate(0deg);\r\n    transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    -webkit-transform: rotate(360deg);\r\n    -moz-transform: rotate(720deg);\r\n    -o-transform: rotate(360deg);\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
